.ant-layout-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	border-bottom: 1px solid #f6f6f6;

	.header-lf {
		display: flex;
		align-items: center;
		.logo{
			width: 50px;
			height: 50px;
		}
		.title{
			color: #ffffff;
			font-size: 15px;
		}
	}

	.header-ri {
		display: flex;
		align-items: center;

		.icon-style {
			margin-right: 22px;
			font-size: 25px;
			line-height: 25px;
			cursor: pointer;
		}

		.username {
			margin: 0 20px 0 0;
			font-size: 15px;
			color: #ffffff;
		}

		.ant-avatar {
			cursor: pointer;
		}
	}
}

.divider {
	margin: 0 0 22px !important;
	font-size: 15px !important;

	.anticon {
		margin-right: 10px;
	}
}

.ant-divider-with-text::before,
.ant-divider-with-text::after {
	border-top: 1px solid #dcdfe6 !important;
}